import React from "react";

const LineApexChartLazy = React.lazy(() => import("./lazy/LineApexChartLazy.js"))

export default (props) => {
    const isSSR = typeof window === "undefined"
    return <>
      {!isSSR && <React.Suspense fallback={<div />}><LineApexChartLazy {...props} /></React.Suspense>}
      </>
}
  
  
