// });

const loadState = () => {
  try {
    const serializedState = localStorage.getItem('econolabs');
    if (serializedState === null) {
      return undefined;
    }
    return JSON.parse(serializedState);
  } catch (err) {
    return undefined
  }
};


export const dataSlice = createSlice({
  name: 'data',
  initialState: {
    data: !!loadState() && !!loadState()?.data?.data ? loadState().data.data : [],
    loading: false,
    hasErrors: false,
  },
  reducers: {
    createDataItem: (state, action) => {
      state.data.push(action.payload);
      //       let newPosts = [...state.posts, action.payload ];
      //       savePostsInBrowser(state.posts);
    },
    empty_data: (state) => {
      state.data = [];      
    },
    // mark_del_post: (state, action) => {
    //   let arrayId = state.posts.findIndex(item => item.id === action.payload.id);
    //   state.posts[arrayId].deleted = !state.posts[arrayId].deleted;
    // },
    getDataItems: state => {
      state.loading = true
    },
    getDataSuccess: (state, { payload }) => {
      state.data = payload
      state.loading = false
      state.hasErrors = false
    },
    saveDataSuccess: (state, { payload }) => {
      //    state.posts = payload
      state.loading = false
      state.hasErrors = false
    },
    getDataFailure: state => {
      state.loading = false
      state.hasErrors = true
    },
  },
});

export const { createDataItem, empty_data, getDataItems, getDataSuccess, saveDataSuccess, getDataFailure  } = dataSlice.actions; //  getDataItems, getDataSuccess, getDataFailure, saveDataSuccess

export const selectUserData = state => state.data;


