import React from 'react';
import Ajv from 'ajv';
import { JSONSchemaBridge } from 'uniforms-bridge-json-schema';
import { AutoForm } from 'uniforms-bootstrap4';

const ajv = new Ajv({ allErrors: true, useDefaults: true });

function createValidator(schema) {
  const validator = ajv.compile(schema);

  return (model) => {
    validator(model);
    return validator.errors?.length ? { details: validator.errors } : null;
  };
}



export default function UniFormLayout(props) {
    const formSchema = new JSONSchemaBridge(props.schema, createValidator(props.schema));

    return <AutoForm schema={formSchema} onSubmit={(model)=>props.setFormObject(model)} model={props.model} />
}