import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";

import UniFormLayout from "../../components/UniFormLayout";

import Modal from "react-bootstrap/Modal";

import {
    selectApplication, set_user_profile
} from "./applicationSlice";

const schema = {
    title: 'Сведения о расчете',
    type: 'object',
    properties: {
        email: { type: 'string' },
        user: { type: 'string' },
        avatarUrl: { type: 'string' }
    },
    required: ['email', 'user'],
};

export default function LoginLogout() {
    const dispatch = useDispatch();
    const [modalShow, setModalShow] = useState(false);
    const user_profile = useSelector(selectApplication);

    function setFormObject(objectFromUniforms) {
        dispatch(set_user_profile(
            {
                email: objectFromUniforms.email,
                user: objectFromUniforms.user,
                avatarUrl: objectFromUniforms.avatarUrl
            }));
          console.log(objectFromUniforms);
    }

    return <>
        <span onClick={() => setModalShow(true)} style={{ marginRight: "1rem" }}>
            {!!user_profile?.user && user_profile.user.length > 0 ? user_profile.user : 'Anonymous'}
        </span>
        <Modal
            //  size="lg"
            show={modalShow}
            onHide={() => setModalShow(false)}
            aria-labelledby="login"
        >
            <Modal.Header closeButton>
                <Modal.Title id="login-lg">Логин</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <UniFormLayout schema={schema} setFormObject={setFormObject} 
                model={user_profile}
                />
                </Modal.Body>
        </Modal>
    </>
}