import React from "react";

const DatalineBarApexChartLazy = React.lazy(() => import("./lazy/DatalineBarApexChartLazy.js"))

export default (props) => {
    const isSSR = typeof window === "undefined"
    return <>
      {!isSSR && (
        <React.Suspense fallback={<div />}><DatalineBarApexChartLazy {...props} /></React.Suspense>
      )}
    </>
}
  
  
