import React from "react";

const DatalineQuotesApexChartLazy = React.lazy(() => import("./lazy/DatalineQuotesApexChartLazy.js"))

export default (props) => {
    const isSSR = typeof window === "undefined"
    return <>
      {!isSSR && (
        <React.Suspense fallback={<div />}><DatalineQuotesApexChartLazy {...props} /></React.Suspense>
      )}
    </>
}
  
  
