import React from "react";
import { useFirebaseNode } from "../hooks/useFirebaseNode";

import { normalizeQuotes } from "../hooks/quotesFunctions";
import ShowRegressionString, { regressionGradient } from "../laboratory/ShowRegressionString";

import ScatterQuotesApexChart from "../mediatemplates/ScatterQuotesApexChart";
import DatalineQuotesApexChart from "../mediatemplates/DatalineQuotesApexChart";

import Image from "react-bootstrap/Image";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";


export default function StudentBetaCalculations() {
    const [activeTicker, setActiveTicker] = React.useState(null);
    const [activePeriod, setActivePeriod] = React.useState(null);

    const { data: moex, loading, error } = useFirebaseNode('moex');

    if (!!loading || !moex) {
        return <div>...</div>;
    }
    if (!!error) {
        return <div>:-(</div>;
    }
    let availableTickers = Object.keys(moex).map((item) => {
        return item
    });
    console.log(activeTicker, activePeriod);

    return <Card bg={"light"} style={{ maxWidth: "95%", margin: "1rem" }}>
        <Card.Header>Анализ данных</Card.Header>
        <Card.Body>
            <div>{availableTickers.map(ticker => <Button
                variant={activeTicker === ticker ? "secondary" : "outline-secondary"}
                className="m-1" key={ticker}
                onClick={() => { setActiveTicker(ticker); setActivePeriod(null) }}
            >{ticker}</Button>)}</div>

            <div>{!!activeTicker && Object.keys(moex[activeTicker]).map(per => <Button
                variant={activePeriod ? "secondary" : "outline-secondary"}
                className="m-1" key={per}
                onClick={() => setActivePeriod(per)}
            >{per}</Button>)}</div>
            <div>{!!activeTicker && !!activePeriod && <ShowCharts
                activeTicker={activeTicker}
                activePeriod={activePeriod}
                quotes={moex?.[activeTicker]?.[activePeriod]}
                imoex={moex.IMOEX[activePeriod]}
            />}</div>
        </Card.Body>
    </Card>
}

function ShowCharts({ activeTicker, activePeriod, quotes, imoex }) {
    const [chartDataReady, setChartDataReady] = React.useState(false);
    const [lineChartData, setLineChartData] = React.useState(null);
    const [scatterChartData, setScatterChartData] = React.useState(null);
   
    React.useEffect(() => {
        setChartDataReady(false);
        if (!!quotes && !!imoex) {

            let datesIndexArrayRedux = Object.keys(imoex)
                .map(quotedate => {
                    let year = quotedate.substring(0, 4);
                    let month = quotedate.substring(4, 6);
                    let day = quotedate.substring(6, 8);
                    let date = + new Date(year, month - 1, day);
                    //        console.log(date);
                    return [date, parseFloat(imoex[quotedate].close)]
                });
            let datesAssetArrayRedux = Object.keys(quotes)
                .map(quotedate => {
                    let year = quotedate.substring(0, 4);
                    let month = quotedate.substring(4, 6);
                    let day = quotedate.substring(6, 8);
                    let date = + new Date(year, month - 1, day);
                    //        console.log(date);
                    return [date, parseFloat(quotes[quotedate].close)]
                });

            let series = [
                {
                    name: 'IMOEX',
                    data: [...datesIndexArrayRedux]
                },
                {
                    name: activeTicker,
                    data: [...datesAssetArrayRedux]
                },
               
            ];

            console.log(series);

            let normalizeQuotesData = normalizeQuotes(series);
            setLineChartData(normalizeQuotesData.series);
            setScatterChartData(normalizeQuotesData.scatterdata);
            setChartDataReady(true);
          
        }
    }, [activeTicker, activePeriod]);

    console.log(quotes);
    console.log(imoex);

    if (!!chartDataReady && !!lineChartData && !!scatterChartData) {
        return <div>
            <DatalineQuotesApexChart series={lineChartData} />
            <ScatterQuotesApexChart series={scatterChartData} />
            <ShowRegressionString arrayOfarrays={scatterChartData} />
        </div>
    }

    return <div>...</div>
}

function ShowMedia(props) {
    const { data: mediaItems, loading, error } = useFirebaseNode(
        props.datafirenode
    );
    //  console.log(props.content, mediaItems);

    if (!!loading || !mediaItems) {
        return <div>...</div>;
    }
    if (!!error) {
        return <div>:-)</div>;
    }

    return (
        <div>
            {Object.keys(mediaItems).map((mediaItem, mediaItemsIndex) => {
                if (
                    mediaItem === "scatterquotesapexchart" ||
                    mediaItem === "scatterquotesapexchartoptions"
                ) { return <GetReadyScatterQuotesApexChart key={mediaItemsIndex} firenode={mediaItems[mediaItem]} /> }
                if (
                    mediaItem === "datalinequotesapexchart" ||
                    mediaItem === "datalinequotesapexchartoptions"
                ) { return <GetReadyDatalineQuotesApexChart key={mediaItemsIndex} firenode={mediaItems[mediaItem]} /> }

                if (mediaItem === "images") { return <ShowImages key={mediaItemsIndex} urls={mediaItems.images.urls} /> }

                return null;
            })}
        </div>
    );
}

function GetReadyScatterQuotesApexChart({ firenode }) {
    let series = Object.keys(firenode.series).map(
        (scatter) => {
            return [
                firenode["series"][scatter][0],
                firenode["series"][scatter][1],
            ];
        }
    );
    //       console.log(series)
    return <ScatterQuotesApexChart series={series} options={
        !!firenode?.options
            ? firenode.options
            : {}
    } />
}



function GetReadyDatalineQuotesApexChart({ firenode }) {
    let series = Object.keys(firenode.series).map((line) => {
        return {
            name: firenode["series"][line]["name"],
            data: Object.keys(
                firenode["series"][line]["data"]
            ).map((line_point) => {
                return [
                    firenode["series"][line]["data"][line_point][0],
                    firenode["series"][line]["data"][line_point][1],
                ];
            }),
        };
    });
    //           console.log(series);
    return <DatalineQuotesApexChart
        series={series}
        options={
            !!firenode?.options
                ? firenode.options
                : {}
        }
    />
}

function ShowImages({ urls }) {
    return <>
        {urls?.[0] ? <Image src={urls[0]} /> : null}
        {urls?.[1] ? <Image src={urls[1]} /> : null}
        {urls?.[2] ? <Image src={urls[2]} /> : null}
        {urls?.[3] ? <Image src={urls[3]} /> : null}
        {urls?.[4] ? <Image src={urls[4]} /> : null}
    </>
}
