import React from "react";

const DataLineBrightColorsLazy = React.lazy(() => import("./lazy/DataLineBrightColorsLazy.js"))

export default (props) => {
    const isSSR = typeof window === "undefined"
    return <>
      {!isSSR && (
        <React.Suspense fallback={<div />}><DataLineBrightColorsLazy {...props} /></React.Suspense>
      )}
    </>
}
  
  
