import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";


import ButtonGroup from "react-bootstrap/ButtonGroup";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import Toast from "react-bootstrap/Toast";

import Tabs from "react-bootstrap/Tabs";
import Tab from "react-bootstrap/Tab";

import UniFormLayout from "../../components/UniFormLayout";

import { createPost, selectPosts, mark_del_post } from "./postsSlice";

import {
  selectSpreadsheetProtoData,
  load_data,
  new_empty_spreadsheet,
  set_spreadsheetTitle,
  createProtoArray
} from "../spreadsheet/spreadsheetSlice";


export default function PostsButtonGroup(props) {
  const dispatch = useDispatch();
  const [showSave, setShowSave] = useState(false);
  const [showOpen, setShowOpen] = useState(false);

  const handleClose = () => {
    setShowSave(false);
    setShowOpen(false);
  };
  const handleSaveShow = () => setShowSave(true);
  const handleOpenShow = () => setShowOpen(true);

  return (
    <>
      <ButtonGroup aria-label="Posts Buttons" size="sm">
        <Button
          variant="outline-secondary"
          onClick={() => dispatch(new_empty_spreadsheet())}
          data-toggle="tooltip"
          data-placement="bottom"
          title="Новый расчет"
        >
          Нов
        </Button>
        <Button
          variant="outline-secondary"
          onClick={() => handleSaveShow()}
          data-toggle="tooltip"
          data-placement="bottom"
          title="Сохранить расчет"
        >
          Сохр
        </Button>
        <Button
          variant="outline-secondary"
          onClick={() => handleOpenShow()}
          data-toggle="tooltip"
          data-placement="bottom"
          title="Открыть расчет"
        >
          Откр
        </Button>
        <Button
          variant="outline-secondary"
          onClick={() => props.toggle_expand_view(!props.expandView)}
          data-toggle="tooltip"
          data-placement="bottom"
          title={props.expandView ? "Свернуть расчет" : "Развернуть расчет"}
        >
          {props.expandView ? "Сверн" : "Разв"}
        </Button>
      </ButtonGroup>
      {showSave ? (
        <SavePostModal
          show={showSave}
          handleClose={handleClose}
          quizString={props?.quizString}
          title={props?.title}
          answer={props?.answer}
          answerIsRight={props?.answerIsRight}
        />
      ) : null}
      {showOpen ? (
        <SelectAndOpenModal show={showOpen} handleClose={handleClose} />
      ) : null}
    </>
  );
}

function SavePostModal(props) {
  const dispatch = useDispatch();
  const content = useSelector(selectSpreadsheetProtoData);
  const [formDataObject, setFormDataObject] = useState({
    title: "",
    comment: "",
  });
  const [editPostTitleComment, doEditPostTitleComment] = useState(true);

  function setFormObject(objectFromUniforms) {
    doEditPostTitleComment(false);
    setFormDataObject(objectFromUniforms);
  }

  const schema = {
    title: "Сведения о расчете",
    type: "object",
    properties: {
      title: { type: "string" },
      comment: { type: "string" },
    },
    required: ["title", "comment"],
  };

  return (
    <Modal
      dialogClassName="modal-90w"
      show={props.show}
      onHide={props.handleClose}
    >
      <Modal.Header closeButton>
        <Modal.Title>Сохранить расчет</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {editPostTitleComment ? (
          <UniFormLayout schema={schema} setFormObject={setFormObject} model={{ title: props?.title }} />
        ) : (
            <blockquote className="blockquote mb-0">
              <p> {formDataObject.title} </p>
              <footer className="blockquote-footer">
                <cite title={formDataObject.comment}>
                  {formDataObject.comment}
                </cite>
              </footer>
            </blockquote>
          )}
      </Modal.Body>
      <Modal.Footer>
        <Button
          variant="outline-secondary"
          onClick={() =>
            dispatch(
              createPost({
                id: Math.floor(Math.random() * 1001),
                title:
                  formDataObject.title.length > 2
                    ? formDataObject.title
                    : props?.title,
                answer: props?.answer,
                answerIsRight: props?.answerIsRight,
                comment: formDataObject.comment,
                type: "spreadsheet",
                content: content,
                quizString: props.quizString,
                deleted: false,
                date: new Intl.DateTimeFormat("ru", {
                  weekday: "short",
                  year: "numeric",
                  month: "short",
                  day: "numeric",
                  hour: "numeric",
                  minute: "numeric",
                }).format(new Date()), //Date().toJSON()
              })
            )
          }
        >
          Сохр
        </Button>
      </Modal.Footer>
    </Modal>
  );
}

function SelectAndOpenModal(props) {
  const dispatch = useDispatch();
  const posts = useSelector(selectPosts).posts;
  const [activeTab, setActiveTab] = useState("posts");

  // useEffect(() => {
  //     dispatch(showPostsInBrowser())
  //   }, [])

  function closeModalopenSpreadsheet(content, title) {
  //  console.log(content)
    dispatch(load_data({ protoData: content }));
    dispatch(set_spreadsheetTitle({ spreadsheetTitle: title }));
  }

  return (
    <Modal size="lg" show={props.show} onHide={props.handleClose}>
      <Modal.Header closeButton>
        <Modal.Title>Открыть расчет</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Tabs
          id="controlled-tab"
          activeKey={activeTab}
          onSelect={(k) => setActiveTab(k)}
        >
          <Tab eventKey="posts" title="Мои расчеты" className="mt-2">
            {!!posts &&
              posts.length > 0 &&
              posts
                .filter((post) => !post.deleted)
                .map((
                  item //; posts
                ) => (
                    <Toast
                      key={item.id}
                      onClose={() => dispatch(mark_del_post({ id: item.id }))}
                    >
                      <Toast.Header
                        onClick={() =>
                          closeModalopenSpreadsheet(createProtoArray(item.content), item.title)
                        }
                      >
                        <span>
                          <svg height="12" width="12" className="mr-2">
                            <circle cx="6" cy="6" r="6" fill="green" />
                          </svg>
                        </span>
                        <span className="mr-1 ml-1">
                          {item.deleted ? <del>{item.title}</del> : item.title}
                        </span>
                        <small>{item.date}</small>
                      </Toast.Header>
                      <Toast.Body>
                        <span>{item.comment}</span>
                      </Toast.Body>
                    </Toast>
                  ))}
          </Tab>

          <Tab
            eventKey="bin"
            title="Корзина"
            disabled={
              !!posts &&
              posts.length > 0 &&
              posts.filter((post) => post.deleted).length === 0
            }
            className="mt-2"
          >
            {!!posts &&
              posts.length > 0 &&
              posts
                .filter((post) => post.deleted)
                .map((
                  item //; posts
                ) => (
                    <Toast
                      key={item.id}
                      onClose={() => dispatch(mark_del_post({ id: item.id }))}
                    >
                      <Toast.Header>
                        <svg height="20" width="20">
                          <circle cx="10" cy="10" r="10" fill="green" />
                        </svg>
                        {item.deleted ? (
                          <del className="mr-auto ml-2">{item.title}</del>
                        ) : (
                            <span className="mr-auto ml-2">{item.title}</span>
                          )}
                        <small>{item.date}</small>
                      </Toast.Header>
                      <Toast.Body>
                        <span>{item.comment}</span>
                      </Toast.Body>
                    </Toast>
                  ))}
          </Tab>
          {/* <Tab eventKey="templates" title="Шаблоны" disabled className="mt-2">
            Шаблонов нет
          </Tab> */}
        </Tabs>
      </Modal.Body>
      {/* <Modal.Footer>
               <Button variant="outline-secondary" onClick={() => props.handleClose()}>S</Button>
        </Modal.Footer> */}
    </Modal>
  );
}

