import React from "react";

const ScatterQuotesApexChartLazy = React.lazy(() => import("./lazy/ScatterQuotesApexChartLazy.js"))

export default (props) => {
    const isSSR = typeof window === "undefined"
    return <>
      {!isSSR && (
        <React.Suspense fallback={<div />}><ScatterQuotesApexChartLazy {...props} /></React.Suspense>
      )}
    </>
}
